
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { RouteComponentProps, useHistory } from "react-router-dom";
import CustomerModel from "../../../Models/CustomerModel";
import { adminCustomerUpdatedAction } from "../../../Redux/AdminCustomerState";
import store from "../../../Redux/Store";
import globals from "../../../Services/Globals";
import tokenAxios from "../../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../../Services/Notification";
import "./UpdateCustomer.css";

interface RouteParam {
    id: string;
}
interface CustomerDetailsProps extends RouteComponentProps<RouteParam> { }

function UpdateCustomer(props: CustomerDetailsProps): JSX.Element {
    const { register, handleSubmit, formState: { errors, isDirty, isValid } } = useForm<CustomerModel>({
        mode: "onTouched"
    });
    const history = useHistory();
    const id = +props.match.params.id;
    const [customer, setCustomer] = useState(
        store.getState().adminCustomerState.adminCustomer.find((c) => c.id === id)
    );
    useEffect(() => {
        // If we don't have a user object - we are not logged in
        if (!store.getState().authState.user) {
            notify.error(ErrMsg.PLS_LOGIN);
            history.push("/login")
        }
    })
    async function send(customer: CustomerModel) {
        console.log(customer);
        try {
            const response = await tokenAxios.post<CustomerModel>(globals.urls.admin + "customer", customer);
            const added = response.data;
            store.dispatch(adminCustomerUpdatedAction(added)); //With Redux
            notify.success(SccMsg.UPDATE_CUSTOMER)
            history.push('/admin')
        }
        catch (err) {
            notify.error(ErrMsg.UPDATE_CUSTOMER);
        }
    }
    return (
        <div className="UpdateCustomer">
            <h2>Update Customer</h2>
            <br />
            <form onSubmit={handleSubmit(send)}>
                {/* <label>Id</label> */}
                <br />
                <input type="hidden" name="id"
                        // type="hidden"
                    value={customer?.id}
                    {...register("id")} 
                   
                    />
                <br />
                <label>First Name</label>
                <br />
                <input type="firstName" name="firstName"
                    value={customer?.firstName}
                    {...register("firstName")}
                  />
                <br />
                <label>Last Name</label>
                <br />
                <input type="lastName" name="lastName"
                    value={customer?.lastName}
                    {...register("lastName")}
                     />
                <br />
                <label>Email</label> <br />
                <input type="email" name="email"
                    defaultValue={customer.email}
                    {...register("email", { required: true })} />
                <br />
                <span>{errors.email?.message}</span>
                {errors.email && <span>missing email</span>}
                <br />

                <label>Password</label> <br />
                <input type="password" name="password"
                    {...register("password", { required: true })} />
                <br />
                {errors.password && <span>missing password</span>}
                <br />

                <button type="submit" color="primary">Update</button>
            </form>
        </div>
    );
}

export default UpdateCustomer;