import CompanyModel from "../Models/CompanyModel";

// Step 1 - Create AppState and manage the collection once and in a centralize place
export class AdminAppState {
    public admin: CompanyModel[] = [];
}

//Step 2 - Define ActionType using enum for all required operations
export enum AdminActionType {
    adminDownloaded = "adminDownloaded",
    adminAdded = "adminAdded",
    adminUpdated = "adminUpdated",
    adminDeleted = "adminDeleted"
}

// Step 3 - Define Action Interface to describe actionAction & payload if needed
export interface AdminAction {
    type: AdminActionType;
    payload?: any;
}

// Step 4 - Export Action Creators functions that gets payload and return relevant Action
export function adminDownloadedAction(admin: CompanyModel[]): AdminAction {
    return { type: AdminActionType.adminDownloaded, payload: admin };
}
export function adminAddedAction(admin: CompanyModel): AdminAction {
    return { type: AdminActionType.adminAdded, payload: admin };
}

export function adminUpdatedAction(admin: CompanyModel): AdminAction {
    return { type: AdminActionType.adminUpdated, payload: admin };
}

export function adminDeletedAction(id: number): AdminAction {
    return { type: AdminActionType.adminDeleted, payload: id };
}

// Step 5 - Reducer function perform the required action
export function adminReducer(currentState: AdminAppState = new AdminAppState(), action: AdminAction): AdminAppState {

    const newState = { ...currentState } //Spread Operator

    switch (action.type) {
        case AdminActionType.adminDownloaded:
            console.log(action.payload);
            newState.admin = action.payload;//ToDo
            break;
        case AdminActionType.adminAdded:
            newState.admin.push(action.payload);//ToDo
            break;

        case AdminActionType.adminUpdated://ToDo
            break;
        case AdminActionType.adminDeleted:
            newState.admin = newState.admin.filter(c => c.id !== action.payload);//ToDo
            break;

    }

    return newState;
}