import CouponModel from "../Models/CouponModel";

// Step 1 - Create AppState and manage the collection once and in a centralize place
export class CustomerPurchaseAppState {
    public customerPurchase: CouponModel[] = [];
}

//Step 2 - Define ActionType using enum for all required operations
export enum CustomerPurchaseActionType {
    customerPurchaseDownloaded = "customerPurchaseDownloaded",
    customerPurchaseAdded = "customerPurchaseAdded",
    customerPurchaseUpdated = "customerPurchaseUpdated",
    customerPurchaseDeleted = "customerPurchaseDeleted"
}

// Step 3 - Define Action Interface to describe actionAction & payload if needed
export interface CustomerPurchaseAction {
    type: CustomerPurchaseActionType;
    payload?: any;
}

// Step 4 - Export Action Creators functions that gets payload and return relevant Action
export function customerPurchaseDownloadedAction(customerPurchase: CouponModel[]): CustomerPurchaseAction {
    return { type: CustomerPurchaseActionType.customerPurchaseDownloaded, payload: customerPurchase };
}
export function customerPurchaseAddedAction(customerPurchase: CouponModel): CustomerPurchaseAction {
    return { type: CustomerPurchaseActionType.customerPurchaseAdded, payload: customerPurchase };
}

export function customerPurchaseUpdatedAction(customerPurchase: CouponModel): CustomerPurchaseAction {
    return { type: CustomerPurchaseActionType.customerPurchaseUpdated, payload: customerPurchase };
}

export function customerPurchaseDeletedAction(id: number): CustomerPurchaseAction {
    return { type: CustomerPurchaseActionType.customerPurchaseDeleted, payload: id };
}

// Step 5 - Reducer function perform the required action
export function customerPurchaseReducer(currentState: CustomerPurchaseAppState = new CustomerPurchaseAppState(), action: CustomerPurchaseAction): CustomerPurchaseAppState {

    const newState = { ...currentState } //Spread Operator

    switch (action.type) {
        case CustomerPurchaseActionType.customerPurchaseDownloaded:
            console.log(action.payload);
            newState.customerPurchase = action.payload;//ToDo
            break;
        case CustomerPurchaseActionType.customerPurchaseAdded:
            newState.customerPurchase.push(action.payload);//ToDo
            break;

        case CustomerPurchaseActionType.customerPurchaseUpdated://ToDo
            break;
        case CustomerPurchaseActionType.customerPurchaseDeleted:
            newState.customerPurchase = newState.customerPurchase.filter(c => c.id !== action.payload);//ToDo
            break;

    }

    return newState;
}