import { useEffect } from "react";
import { useForm } from "react-hook-form";
import { RouteComponentProps, useHistory } from "react-router-dom";
import CategoryType from "../../Models/CategoryTypeModel";
import CouponModel from "../../Models/CouponModel";
import { companyAddedAction } from "../../Redux/CompanyState";
import store from "../../Redux/Store";
import globals from "../../Services/Globals";
import tokenAxios from "../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../Services/Notification";

import "./AddCompanyCoupon.css";
interface RouteParam{
    id: string;
}

interface CouponDetailsProps extends RouteComponentProps<RouteParam>{}

function AddCompanyCoupon(): JSX.Element {
    const { register, handleSubmit, formState: { errors, isDirty, isValid } } = useForm<CouponModel>({
        mode: "onTouched"
    });

    const history = useHistory();

    useEffect(() => {
        // If we don't have a user object - we are not logged in
        if (!store.getState().authState.user) {
            notify.error(ErrMsg.PLS_LOGIN);
            history.push("/login")
        }
    })
    async function send(coupon: CouponModel) {
        console.log(coupon);
        try {
            let fdate = new Date(coupon.startDate).setHours(0, 0, 0, 0);
            let tdate = new Date(coupon.endDate).setHours(0, 0, 0, 0);
            let cdate = new Date().setHours(0, 0, 0, 0);

            if (fdate.valueOf() > tdate.valueOf()) {
                alert("endDate can't be less then startDate")
                return;
            } else if (fdate.valueOf() < cdate.valueOf()) {
                alert("startDate can't be less then current Date")
                return;
            }
            const response = await tokenAxios.post<CouponModel>(globals.urls.companies + "coupon", coupon);
            const added = response.data;
            console.log(" added object " + added);
            store.dispatch(companyAddedAction(added)); //With Redux
            notify.success(SccMsg.ADD_COUPON);
            history.push('/companies')
        }
        catch (err) {
            notify.error(ErrMsg.ADD_COUPON);
        }
    }

    return (
        <div className="AddCompanyCoupon">
            <h2>Add Company Coupon</h2>
            <form onSubmit={handleSubmit(send)}>
                <label>Category</label>
                <br />
                <select name="category" {...register("category")}>
                    <option value={CategoryType.food}>FOOD</option>
                    <option value={CategoryType.electricity}>ELECTRICITY</option>
                    <option value={CategoryType.vacation}>VACATION</option>
                    <option value={CategoryType.cars}>CARS</option>

                </select>
                <br />

                <br />
                <label>company ID</label>
                <br />
                <input type="text" name="company ID"
                    {...register("companyID", {
                        
                        required: true,
                    
                    })} />
                <br />
                <br />
                <label>Title</label>
                <br />
                <input type="text" name="title"
                    {...register("title", {
                        required: true,
                        minLength: 2
                    })} />
                <br />
                <br />
                <label>Price</label>
                <br />
                <input type="double" name="price"
                    {...register("price", {
                        required: true,
                    })} />
                <br />
                <br />
                <label>Amount</label>
                <br />
                <input type="amount" name="amount"
                    {...register("amount", {
                        required: true,
                        min: 0
                    })} />
                <br />
                {errors.amount?.type === 'min' && <span>minimum amount should be zero</span>}
                <br />
                <label>Description</label>
                <br />
                <input type="text" name="description"
                    {...register("description", {
                        required: true,
                        minLength: 2
                    })} />
                <br />
                {errors.description?.type === 'minLength' && <span>minimum description length is 10</span>}
                <br />
                <label>Start Date</label>
                <br />
                <input type="date" name="start_date"
                    {...register("startDate", {
                        required: true,
                    })} />
                <br />
                <br />
                <label>End Date</label>
                <br />
                <input type="date" name="end_date"
                    {...register("endDate", {
                        required: true,
                    })} />
                <br />
                <br />
                <button type="submit">Add Coupon</button>
            </form>
        </div>
    );
}

export default AddCompanyCoupon;