import { useEffect } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import CompanyModel from "../../../Models/CompanyModel";
import { adminAddedAction } from "../../../Redux/AdminState";
import store from "../../../Redux/Store";
import globals from "../../../Services/Globals";
import tokenAxios from "../../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../../Services/Notification";

import "./AddCompany.css";

function AddCompany(): JSX.Element {
    const { register, handleSubmit, formState: { errors, isDirty, isValid } } = useForm<CompanyModel>({
        mode: "onTouched"
    });
    // const onSubmit: SubmitHandler<CompanyModel> = data => console.log(data);
    const history = useHistory();

    useEffect(() => {
        // If we don't have a user object - we are not logged in
        if (!store.getState().authState.user) {
            notify.error(ErrMsg.PLS_LOGIN);
            history.push("/login")
        }
    })

    async function send(company: CompanyModel) {
        console.log(company);
        try {
            // const formData = new FormData();
            // formData.append("name", company.name);
            // formData.append("email", company.email);
            // formData.append("password", company.password);

            //Sending token without interceptor
            // const headers = {"authorization": store.getState().authState.user.token}
            // const response = await axios.post<CompanyModel>(globals.urls.admin+"/company",company,{headers});
            //Sending token with interceptor
            const response = await tokenAxios.put<CompanyModel>(globals.urls.admin + "company", company);
            const added = response.data;
            store.dispatch(adminAddedAction(added)); //With Redux
            notify.success(SccMsg.ADD_COMPANY)
            history.push('/admin')
        }
        catch (err) {
            console.log(ErrMsg.ADD_COMPANY);
            //  notify.error(err.message);
        }
    }
    return (
        <div className="AddCompany">
            <h2>Add Company</h2>
            <form onSubmit={handleSubmit(send)}>
                <label>Name</label>
                <br />
                <input type="name" name="name"
                    {...register("name", {
                        required: true,
                        minLength: 2
                    })} />
                <br />
                {errors.name?.type === 'required' && <span>missing name</span>}
                {errors.name?.type === 'minLength' && <span>name is too short</span>}
                <br />

                <label>Email</label> <br />
                <input type="email" name="email"
                    {...register("email")} />
                <br />
                <span>{errors.email?.message}</span>
                <br />

                <label>Password</label> <br />
                <input type="password" name="password"
                    {...register("password", { required: true })} />

                <br />
                {errors.password && <span>missing password</span>}
                <br />

                <button type="submit" disabled={!isDirty || !isValid} color="primary">Add</button>
            </form>
        </div>
    );
}

export default AddCompany;