import CustomerModel from "../Models/CustomerModel";

// Step 1 - Create AppState and manage the collection once and in a centralize place
export class AdminCustomerAppState {
    public adminCustomer: CustomerModel[] = [];
}

//Step 2 - Define ActionType using enum for all required operations
export enum AdminCustomerActionType {
    adminCustomerDownloaded = "adminCustomerDownloaded",
    adminCustomerAdded = "adminCustomerAdded",
    adminCustomerUpdated = "adminCustomerUpdated",
    adminCustomerDeleted = "adminCustomerDeleted"
}

// Step 3 - Define Action Interface to describe actionAction & payload if needed
export interface AdminCustomerAction {
    type: AdminCustomerActionType;
    payload?: any;
}

// Step 4 - Export Action Creators functions that gets payload and return relevant Action
export function adminCustomerDownloadedAction(adminCustomer: CustomerModel[]): AdminCustomerAction {
    return { type: AdminCustomerActionType.adminCustomerDownloaded, payload: adminCustomer };
}
export function adminCustomerAddedAction(adminCustomer: CustomerModel): AdminCustomerAction {
    return { type: AdminCustomerActionType.adminCustomerAdded, payload: adminCustomer };
}

export function adminCustomerUpdatedAction(adminCustomer: CustomerModel): AdminCustomerAction {
    return { type: AdminCustomerActionType.adminCustomerUpdated, payload: adminCustomer };
}

export function adminCustomerDeletedAction(id: number): AdminCustomerAction {
    return { type: AdminCustomerActionType.adminCustomerDeleted, payload: id };
}

// Step 5 - Reducer function perform the required action
export function adminCustomerReducer(currentState: AdminCustomerAppState = new AdminCustomerAppState(), action: AdminCustomerAction): AdminCustomerAppState {

    const newState = { ...currentState } //Spread Operator

    switch (action.type) {
        case AdminCustomerActionType.adminCustomerDownloaded:
            console.log(action.payload);
            newState.adminCustomer = action.payload;//ToDo
            break;
        case AdminCustomerActionType.adminCustomerAdded:
            newState.adminCustomer.push(action.payload);//ToDo
            break;

        case AdminCustomerActionType.adminCustomerUpdated://ToDo
            break;
        case AdminCustomerActionType.adminCustomerDeleted:
            newState.adminCustomer = newState.adminCustomer.filter(c => c.id !== action.payload);//ToDo
            break;

    }

    return newState;
}