import { combineReducers, createStore } from "redux";
import { adminCustomerReducer } from "./AdminCustomerState";
import { adminReducer } from "./AdminState";
import { authReducer } from "./AuthState";
import { companyReducer } from "./CompanyState";
import { customerPurchaseReducer } from "./CustomerPurchseState";
import { customerReducer } from "./CustomerState";



const reducers = combineReducers({ adminState: adminReducer, adminCustomerState: adminCustomerReducer, companyState: companyReducer, CustomerState: customerReducer, customerPurchaseState: customerPurchaseReducer, authState: authReducer});
const store = createStore(reducers)


export default store;