import { grey } from "@material-ui/core/colors";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { RouteComponentProps, useHistory } from "react-router-dom";
import CompanyModel from "../../../Models/CompanyModel";
import { adminUpdatedAction } from "../../../Redux/AdminState";
import store from "../../../Redux/Store";
import globals from "../../../Services/Globals";
import tokenAxios from "../../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../../Services/Notification";
import "./UpdateCompany.css";

interface RouteParam {
    id: string;
}
interface CompanyDetailsProps extends RouteComponentProps<RouteParam> { }

function UpdateCompany(props: CompanyDetailsProps): JSX.Element {
    const { register, handleSubmit, formState: { errors, isDirty, isValid } } = useForm<CompanyModel>({
        mode: "onTouched"
    });
    const history = useHistory();
    const id = +props.match.params.id;
    const [company, setCompany] = useState(
        store.getState().adminState.admin.find((c) => c.id === id)
    );
    useEffect(() => {
        // If we don't have a user object - we are not logged in
        if (!store.getState().authState.user) {
            notify.error(ErrMsg.PLS_LOGIN);
            history.push("/login")
        }
    })
    async function send(company: CompanyModel) {
        console.log(company);
        try {
            const response = await tokenAxios.post<CompanyModel>(globals.urls.admin + "company", company);
            const added = response.data;
            store.dispatch(adminUpdatedAction(added)); //With Redux
            notify.success(SccMsg.UPDATE_COMPANY)
            history.push('/admin')
        }
        catch (err) {
            notify.error(ErrMsg.UPDATE_COMPANY);
        }
    }
    return (
        <div className="UpdateCompany">
            <h2>Update Company</h2>
            {/* <br /> */}
            <form onSubmit={handleSubmit(send)}>
            {/* <label>Id</label> */}
                {/* <br /> */}
                <input type="hidden" name="id"
                    value={company?.id}
                    {...register("id")} 
                    color={"grey"}
                    />
                <br />
                <label>Name</label>
                <br />
                <input type="name" name="name"
                    value={company?.name}
                    {...register("name")}

                     />
                <br />
                <label>Email</label> <br />
                <input type="email" name="email"
                    defaultValue={company.email}
                    {...register("email", { required: true })} />
                <br />
                <span>{errors.email?.message}</span>
                {errors.email && <span>missing email</span>}
                <br />

                <label>Password</label> <br />
                <input type="password" name="password"
                    {...register("password", { required: true })} />
                <br />
                {errors.password && <span>missing password</span>}
                <br />

                <button type="submit" color="primary">Update</button>
            </form>
        </div>
    );
}

export default UpdateCompany;