import axios from "axios";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import CredentialsModel from "../../../Models/CredentialsModel";
import LoginModel from "../../../Models/LoginModel";
import { loginAction } from "../../../Redux/AuthState";
import store from "../../../Redux/Store";
import globals from "../../../Services/Globals";
import notify from "../../../Services/Notification";
import "./Login.css";

function Login(): JSX.Element {
    const history = useHistory(); //Redirect function
    const { register, handleSubmit, formState: {errors} } = useForm<CredentialsModel>();

    async function send(credentials: CredentialsModel) {
      console.log(credentials);
      
  
      try {
          let url = "";
          console.log(credentials.clientType);
          switch (credentials.clientType) {
              case 'ADMINISTRATOR':
                  url = globals.urls.admin + "login";
                  break;
              case 'COMPANY':
                  url = globals.urls.companies + "login";
                  break;
              case 'CUSTOMER':
                  url = globals.urls.customers + "login";
                  break;
              default:
                  history.push("/home")
          }
          console.log(url);
          const response = await axios.post<LoginModel>(url, credentials);
          console.log(response.data);
          store.dispatch(loginAction(response.data));
        //   alert(JSON.stringify(response.data))
          console.log(credentials.clientType);
          switch (credentials.clientType) {
              case 'ADMINISTRATOR':
                  history.push("/admin")
                  return response.data;
              case 'COMPANY':
                  history.push("/companies")
                  return response.data;
              case 'CUSTOMER':
                  history.push("/customers")
                  return response.data;
              default:
                  history.push("/home")
          }
          notify.success("ok");
      }
      catch (err) {
          console.log(err);
          notify.error("no no");
      }

  }



    return (
        <div className="Login">
            <h2>Login</h2>
        
               <form onSubmit={handleSubmit(send)}>
               <select {...register("clientType", { required: {value:true,message:'Missing User Type'} })}>
         <option value="" disabled>User Type</option>
         <option value="ADMINISTRATOR">Administrator</option>
          <option value="CUSTOMER">Customer</option>
          <option value="COMPANY">Company</option>
        </select>
        <br />
        <input
          type="text"
          placeholder="Email"
          {...register("email", { required: true, pattern: /^\S+@\S+$/i })}
        />
        <br />
        <input
          type="password"
          placeholder="Password"
          {...register("password", {
            required: true,
            minLength: 4,
            maxLength: 22,
          })}
        />
<br />

        <input type="submit" value="Login"/>

      </form>
			
        </div>
    );
}

export default Login;