import { Component } from "react";
import { NavLink, Redirect } from "react-router-dom";
import CompanyModel from "../../../Models/CompanyModel";
import { adminDeletedAction, adminDownloadedAction } from "../../../Redux/AdminState";
import store from "../../../Redux/Store";
import tokenAxios from "../../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../../Services/Notification";
import { Unsubscribe } from "redux";
import "./Admin.css";
import CustomerModel from "../../../Models/CustomerModel";
import { adminCustomerDeletedAction, adminCustomerDownloadedAction } from "../../../Redux/AdminCustomerState";
import globals from "../../../Services/Globals";


interface AdminState {
    companies: CompanyModel[];
    customers: CustomerModel[];
}

class Admin extends Component<{}, AdminState> {
    private unsubscribe: Unsubscribe;

    public constructor(props: {}) {
        super(props);
        this.state = {
            companies: store.getState().adminState.admin,
            customers: store.getState().adminCustomerState.adminCustomer,
        };
    }

    public async componentDidMount(): Promise<void> {
        this.unsubscribe = store.subscribe(() => {
            this.setState({
                companies: store.getState().adminState.admin,
                customers: store.getState().adminCustomerState.adminCustomer
            });
        });

        try {
            const response = await tokenAxios.get<CompanyModel[]>(globals.urls.admin +"companies"); //async request
            console.log(response.status);
            store.dispatch(adminDownloadedAction(response.data)); //update global state
            this.setState({ companies: response.data }); //update local state
            notify.success(SccMsg.GET_COMPANIES);
        } catch (err) {

            notify.error(ErrMsg.GET_COMPANIES);
        }

        try {
            const response = await tokenAxios.get<CompanyModel[]>(globals.urls.admin + "customers"); //async request
            console.log(response.status);
            store.dispatch(adminCustomerDownloadedAction(response.data)); //update global state
            this.setState({ customers: response.data }); //update local state
            notify.success(SccMsg.GET_CUSTOMERS);
        } catch (err) {

            notify.error(ErrMsg.GET_CUSTOMERS);
        }
    }

    public componentWillUnmount(): void {
        this.unsubscribe();
    }

    private async delete(id: number) {
        const res = window.confirm(
            "Are you sure you want to delete id=" + id + "?"
        );
        if (res) {
            try {
                await tokenAxios.delete<any>(globals.urls.admin + "company/" + id);
                store.dispatch(adminDeletedAction(id)); // global state
                this.setState({ companies: store.getState().adminState.admin }); //local state
                notify.success(SccMsg.DELETE_COMPANY)
            } catch (err) {
                notify.error(ErrMsg.DELETE_COMPANY);
            }
        }
    }
    private async deleteCustomer(id: number) {
        const res = window.confirm(
            "Are you sure you want to delete id=" + id + "?"
        );
        if (res) {
            try {
                await tokenAxios.delete<any>(globals.urls.admin + "customer/" + id);
                store.dispatch(adminCustomerDeletedAction(id)); // global state
                this.setState({ customers: store.getState().adminCustomerState.adminCustomer }); //local state
                notify.success(SccMsg.DELETE_CUSTOMER)
            } catch (err) {
                notify.error(ErrMsg.DELETE_CUSTOMER);
            }
        }
    }
    public render(): JSX.Element {
        return (
            <div className="Admin">
                <h1>Companies</h1>
                {this.state.companies.length === 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>Email</th>
                                <th>Password</th>
                                <th>
                                    Actions{" "}
                                    <NavLink style={{ textDecoration: "none" }} to="admin/company/add">
                                        
                                    </NavLink>
                                </th>
                            </tr>
                        </thead>
                    </table>
                )}
                {this.state.companies.length !== 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>Name</th>
                                <th>Email</th>
                                <th>Password</th>
                                <th>
                                    Actions{" "}
                                    <NavLink style={{ textDecoration: "none" }} to="admin/company/add">
                                        <a className="plus">+</a>
                                    </NavLink>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.companies.map((c) => (
                                <tr key={c.id}>
                                    <td>{c.id}</td>
                                    <td>{c.name}</td>
                                    <td>{c.email}</td>
                                    <td>{c.password}</td>
                                    <td>
                                        <button onClick={() => this.delete(c.id)}></button>
                                        <NavLink
                                            style={{ textDecoration: "none" }}
                                            to={"admin/company/update/" + c.id}
                                        >  </NavLink>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}
                <br />

                <h1>Customers</h1>
                {this.state.customers.length === 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>First Name</th>
                                <th>Last Name</th>
                                <th>Email</th>
                                <th>Password</th>
                                <th>
                                    Actions{" "}
                                    <NavLink style={{ textDecoration: "none" }} to="admin/customer/add">
                                        
                                    </NavLink>
                                </th>
                            </tr>
                        </thead>
                    </table>
                )}
                {this.state.customers.length !== 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>First Name</th>
                                <th>Last Name</th>
                                <th>Email</th>
                                <th>Password</th>
                                <th>
                                    Actions{" "}
                                    <NavLink style={{ textDecoration: "none" }} to="admin/customer/add">
                                        <a className="plus">+</a>
                                    </NavLink>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.customers.map((c) => (
                                <tr key={c.id}>
                                    <td>{c.id}</td>
                                    <td>{c.firstName}</td>
                                    <td>{c.lastName}</td>
                                    <td>{c.email}</td>
                                    <td>{c.password}</td>
                                    <td>
                                        <button onClick={() => this.deleteCustomer(c.id)}></button>
                                        <NavLink
                                            style={{ textDecoration: "none" }}
                                            to={"admin/customer/update/" + c.id}
                                        >  </NavLink>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}

            </div>
        );
    }
}

export default Admin;