import { useEffect } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import CustomerModel from "../../../Models/CustomerModel";
import { adminCustomerAddedAction } from "../../../Redux/AdminCustomerState";
import store from "../../../Redux/Store";
import globals from "../../../Services/Globals";
import tokenAxios from "../../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../../Services/Notification";

import "./AddCustomer.css";

function AddCustomer(): JSX.Element {
    const { register, handleSubmit, formState: { errors, isDirty, isValid } } = useForm<CustomerModel>({
        mode: "onTouched"
    });
    // const onSubmit: SubmitHandler<CustomerModel> = data => console.log(data);
    const history = useHistory();

    useEffect(() => {
        // If we don't have a user object - we are not logged in
        if (!store.getState().authState.user) {
            notify.error(ErrMsg.PLS_LOGIN);
            history.push("/login")
        }
    })

    async function send(customer: CustomerModel) {
        console.log(customer);
        try {

            const response = await tokenAxios.put<CustomerModel>(globals.urls.admin + "customer", customer);
            const added = response.data;
            store.dispatch(adminCustomerAddedAction(added)); //With Redux
            notify.success(SccMsg.ADD_CUSTOMER)
            history.push('/admin')
        }
        catch (err) {
            notify.error(ErrMsg.ADD_CUSTOMER);
        }
    }
    return (
        <div className="AddCustomer">
            <h2>Add Customer</h2>
            <form onSubmit={handleSubmit(send)}>
                <label>First Name</label>
                <br />
                <input type="firstName" name="firstName"
                    {...register("firstName", {
                        required: true,
                        minLength: 2
                    })} />
                <br />
                {errors.firstName?.type === 'required' && <span>missing firstName</span>}
                {errors.firstName?.type === 'minLength' && <span>firstName is too short</span>}
                <br />

                <label>Last Name</label>
                <br />
                <input type="lastName" name="lastName"
                    {...register("lastName", {
                        required: true,
                        minLength: 2
                    })} />
                <br />
                {errors.lastName?.type === 'required' && <span>missing lastName</span>}
                {errors.lastName?.type === 'minLength' && <span>lastName is too short</span>}
                <br />

                <label>Email</label> <br />
                <input type="email" name="email"
                    {...register("email")} />
                <br />
                <span>{errors.email?.message}</span>
                <br />

                <label>Password</label> <br />
                <input type="password" name="password"
                    {...register("password", { required: true })} />

                <br />
                {errors.password && <span>missing password</span>}
                <br />

                <button type="submit" disabled={!isDirty || !isValid} color="primary">Add</button>
            </form>
        </div>
    );
}

export default AddCustomer;