import { useEffect } from "react";
import { useState } from "react";
import { useForm } from "react-hook-form";
import { RouteComponentProps, useHistory } from "react-router-dom";
import CouponModel from "../../../Models/CouponModel";
import { companyUpdatedAction } from "../../../Redux/CompanyState";
import store from "../../../Redux/Store";
import globals from "../../../Services/Globals";
import tokenAxios from "../../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../../Services/Notification";
import "./UpdateCompanyCoupon.css";


interface RouteParam {
    id: string;
}
interface CouponDetailsProps extends RouteComponentProps<RouteParam> { }

function UpdateCompanyCoupon(props: CouponDetailsProps): JSX.Element {
    const { register, handleSubmit, formState: { errors, isDirty, isValid } } = useForm<CouponModel>({
        mode: "onTouched"
    });
    const history = useHistory();
    const id = +props.match.params.id;
    const [coupon, setCompany] = useState(
        store.getState().companyState.company.find((c) => c.id === id)
    );
    useEffect(() => {
        // If we don't have a user object - we are not logged in
        if (!store.getState().authState.user) {
            notify.error(ErrMsg.PLS_LOGIN);
            history.push("/login")
        }
    })

    async function send(coupon: CouponModel) {

        console.log(coupon);
        try {
            let fdate = new Date(coupon.startDate).setHours(0, 0, 0, 0);
            let tdate = new Date(coupon.endDate).setHours(0, 0, 0, 0);
            let cdate = new Date().setHours(0, 0, 0, 0);
            if (fdate.valueOf() > tdate.valueOf()) {
                alert("endDate can't be less then startDate")
                return;
            } else if (tdate.valueOf() < cdate.valueOf()) {
                alert("startDate can't be less then current Date")
                return;
            }
            coupon.image = "none";
            coupon.id = +props.match.params.id;
            const response = await tokenAxios.put<CouponModel>(globals.urls.companies + "coupons", coupon);
            const updated = response.data;
            store.dispatch(companyUpdatedAction(updated)); //With Redux
            notify.success(SccMsg.UPDATE_COUPON)
            history.push('/companies')
        }
        catch (err) {
            notify.error(ErrMsg.UPDATE_COUPON);
        }
    }
    function getDate(date: Date): string {
        const myDate = new Date(date);
        const year = myDate.getFullYear();
        const month = ("0" + (myDate.getMonth() + 1)).slice(-2);
        const day = ("0" + myDate.getDate()).slice(-2);
        const newDate = year + "-" + month + "-" + day;
        return newDate;
    }
    return (
        <div className="UpdateCompanyCoupon">
            <h2>Update Company Coupon</h2>
            <br />
            <form onSubmit={handleSubmit(send)}>

                <label>Category</label>
                <br />
                <input type="category" name="category"
                    value={coupon.category}
                    {...register("category")} />
                <br />

                <label>Id</label>
                <br />
                <input type="number" name="id"
                    value={coupon.id}
                    {...register("id")} />
                <br />

                <label>Company Id</label>
                <br />
                <input type="text" name="companyID"
                    value={coupon.companyID}
                    {...register("companyID")} />
                <br />

                <label>Title</label>
                <br />
                <input type="text" name="title"
                    value={coupon.title}
                    {...register("title", {
                        required: true,
                        minLength: 2
                    })} />
                <br />

                <label>Price</label>
                <br />
                <input type="double" name="price"
                    defaultValue={coupon.price}
                    {...register("price", {
                        required: true,
                    })} />
                <br />

                <label>Amount</label>
                <br />
                <input type="number" name="amount"
                    defaultValue={coupon.amount}
                    {...register("amount", {
                        required: true,
                    })} />
                <br />


                <label>Description</label>
                <br />
                <input type="text" name="description"
                    defaultValue={coupon.description}
                    {...register("description", {
                        required: true,
                        minLength: 2
                    })} />
                <br />

                <label>Start Date</label>
                <br />
                <input type="date" name="startDate"
                    // defaultValue={coupon.start_date}
                    defaultValue={getDate(coupon.startDate)}
                    {...register("startDate", {
                        required: true,
                    })} />
                <br />
                <label>End Date</label>
                <br />
                <input type="date" name="endDate"
                    defaultValue={getDate(coupon.endDate)}
                    {...register("endDate", {
                        required: true,
                    })} />
                <br />

                <button type="submit" color="primary">Update coupon</button>
            </form>
        </div>
    );
}

export default UpdateCompanyCoupon;