import { Component } from "react";
import { NavLink } from "react-router-dom";
import { Unsubscribe } from "redux";
import CouponModel from "../../../Models/CouponModel";
import { companyDeletedAction, companyDownloadedAction } from "../../../Redux/CompanyState";
import store from "../../../Redux/Store";
import globals from "../../../Services/Globals";
import tokenAxios from "../../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../../Services/Notification";
import "./Company.css";

interface CompanyState {
    coupons: CouponModel[];
}

class Company extends Component<{}, CompanyState> {
    private unsubscribe: Unsubscribe;

    public constructor(props: {}) {
        super(props);
        this.state = {
            coupons: store.getState().companyState.company,
        };
    }

    public async componentDidMount(): Promise<void> {
        this.unsubscribe = store.subscribe(() => {
            this.setState({ coupons: store.getState().companyState.company });
        });

        try {
            const response = await tokenAxios.get<CouponModel[]>(globals.urls.companies + "coupons"); //async request
            console.log(response.status);
            store.dispatch(companyDownloadedAction(response.data)); //update global state
            this.setState({ coupons: response.data }); //update local state
            notify.success(SccMsg.GET_COUPONS);
        } catch (err) {
            notify.error(ErrMsg.GET_COUPONS);
        }
    }
    public componentWillUnmount(): void {
        this.unsubscribe();
    }
    private async delete(id: number) {
        const res = window.confirm(
            "Are you sure you want to delete id=" + id + "?"
        );
        if (res) {
            try {
                await tokenAxios.delete<any>(globals.urls.companies + "coupons/" + id);
                store.dispatch(companyDeletedAction(id)); // global state
                this.setState({ coupons: store.getState().companyState.company }); //local state
                notify.success(SccMsg.DELETE_COUPON)
            } catch (err) {
                notify.error(ErrMsg.DELETE_COUPON);
            }
        }
    }
    getDate(date: Date): string {
        const myDate = new Date(date);
        const year = myDate.getFullYear();
        const month = ("0" + (myDate.getMonth() + 1)).slice(-2);
        const day = ("0" + myDate.getDate()).slice(-2);
        const newDate = day + "-" + month + "-" + year;
        return newDate;
    }
    public render(): JSX.Element {
        return (
            <div className="Company">
                <h1>Company Coupons</h1>
                {this.state.coupons.length === 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>COMPANY ID</th>
                                <th>CATEGORY</th>
                                <th>TITLE</th>
                                <th>PRICE</th>
                                <th>AMOUNT</th>
                                <th>DESCRIPTION</th>
                                <th>START_DATE</th>
                                <th>END_DATE</th>
                                <th>
                                    Actions{" "}
                                    <NavLink style={{ textDecoration: "none" }} to="/companies/coupon/add">
                                        <a className="plus">+</a>
                                    </NavLink>
                                </th>
                            </tr>
                        </thead>
                    </table>
                )}
                {this.state.coupons.length !== 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>Id</th>
                                <th>COMPANY ID</th>
                                <th>CATEGORY</th>
                                <th>TITLE</th>
                                <th>PRICE</th>
                                <th>AMOUNT</th>
                                <th>DESCRIPTION</th>
                                <th>START_DATE</th>
                                <th>END_DATE</th>
                                <th>
                                    Actions{" "}
                                    <NavLink style={{ textDecoration: "none" }} to="/companies/coupon/add">
                                        <a className="plus">+</a>
                                    </NavLink>
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.coupons.map((c) => (
                                <tr key={c.id}>
                                    <td>{c.id}</td>
                                    <td>{c.companyID}</td>
                                    <td>{c.category}</td>
                                    <td>{c.title}</td>
                                    <td>{c.price}</td>
                                    <td>{c.amount}</td>
                                    <td>{c.description}</td>
                                    <td>{this.getDate(c.startDate)}</td>
                                    <td>{this.getDate(c.endDate)}</td>
                                    <td>
                                        <button onClick={() => this.delete(c.id)}> </button>
                                        <NavLink
                                            style={{ textDecoration: "none" }}
                                            to={"companies/coupon/update/" + c.id}
                                        >  </NavLink>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}
            </div>
        );
    }
}

export default Company;