import CouponModel from "../Models/CouponModel";

// Step 1 - Create AppState and manage the collection once and in a centralize place
export class CompanyAppState {
    public company: CouponModel[] = [];
}

//Step 2 - Define ActionType using enum for all required operations
export enum CompanyActionType {
    companyDownloaded = "companyDownloaded",
    companyAdded = "companyAdded",
    companyUpdated = "companyUpdated",
    companyDeleted = "companyDeleted"
}

// Step 3 - Define Action Interface to describe actionAction & payload if needed
export interface CompanyAction {
    type: CompanyActionType;
    payload?: any;
}

// Step 4 - Export Action Creators functions that gets payload and return relevant Action
export function companyDownloadedAction(company: CouponModel[]): CompanyAction {
    return { type: CompanyActionType.companyDownloaded, payload: company };
}
export function companyAddedAction(company: CouponModel): CompanyAction {
    return { type: CompanyActionType.companyAdded, payload: company };
}

export function companyUpdatedAction(company: CouponModel): CompanyAction {
    return { type: CompanyActionType.companyUpdated, payload: company };
}

export function companyDeletedAction(id: number): CompanyAction {
    return { type: CompanyActionType.companyDeleted, payload: id };
}

// Step 5 - Reducer function perform the required action
export function companyReducer(currentState: CompanyAppState = new CompanyAppState(), action: CompanyAction): CompanyAppState {

    const newState = { ...currentState } //Spread Operator

    switch (action.type) {
        case CompanyActionType.companyDownloaded:
            console.log(action.payload);
            newState.company = action.payload;//ToDo
            break;
        case CompanyActionType.companyAdded:
            newState.company.push(action.payload);//ToDo
            break;

        case CompanyActionType.companyUpdated://ToDo
            break;
        case CompanyActionType.companyDeleted:
            newState.company = newState.company.filter(c => c.id !== action.payload);//ToDo
            break;

    }

    return newState;
}