import CouponModel from "../Models/CouponModel";

// Step 1 - Create AppState and manage the collection once and in a centralize place
export class CustomerAppState {
    public customer: CouponModel[] = [];
}

//Step 2 - Define ActionType using enum for all required operations
export enum CustomerActionType {
    customerDownloaded = "customerDownloaded",
    customerAdded = "customerAdded",
    customerUpdated = "customerUpdated",
    customerDeleted = "customerDeleted"
}

// Step 3 - Define Action Interface to describe actionAction & payload if needed
export interface CustomerAction {
    type: CustomerActionType;
    payload?: any;
}

// Step 4 - Export Action Creators functions that gets payload and return relevant Action
export function customerDownloadedAction(customer: CouponModel[]): CustomerAction {
    return { type: CustomerActionType.customerDownloaded, payload: customer };
}
export function customerAddedAction(customer: CouponModel): CustomerAction {
    return { type: CustomerActionType.customerAdded, payload: customer };
}

export function customerUpdatedAction(customer: CouponModel): CustomerAction {
    return { type: CustomerActionType.customerUpdated, payload: customer };
}

export function customerDeletedAction(id: number): CustomerAction {
    return { type: CustomerActionType.customerDeleted, payload: id };
}

// Step 5 - Reducer function perform the required action
export function customerReducer(currentState: CustomerAppState = new CustomerAppState(), action: CustomerAction): CustomerAppState {

    const newState = { ...currentState } //Spread Operator

    switch (action.type) {
        case CustomerActionType.customerDownloaded:
            console.log(action.payload);
            newState.customer = action.payload;//ToDo
            break;
        case CustomerActionType.customerAdded:
            newState.customer.push(action.payload);//ToDo
            break;

        case CustomerActionType.customerUpdated://ToDo
            break;
        case CustomerActionType.customerDeleted:
            newState.customer = newState.customer.filter(c => c.id !== action.payload);//ToDo
            break;

    }

    return newState;
}