import { Component } from "react";
import { Unsubscribe } from "redux";
import ClientType from "../../../Models/ClientTypeModel";

import CouponModel from "../../../Models/CouponModel";
import LoginModel from "../../../Models/LoginModel";

import { customerPurchaseAddedAction, customerPurchaseDownloadedAction } from "../../../Redux/CustomerPurchseState";
import { customerDownloadedAction } from "../../../Redux/CustomerState";
import store from "../../../Redux/Store";
import globals from "../../../Services/Globals";
import tokenAxios from "../../../Services/InterceptorAxios";
import notify, { ErrMsg, SccMsg } from "../../../Services/Notification";
import "./Customer.css";

interface CustomerState {
    coupons: CouponModel[];
    couponsPurchase: CouponModel[];
    user: LoginModel;

}

class Customer extends Component<{}, CustomerState> {

    private unsubscribe: Unsubscribe;

    public constructor(props: {}) {
        super(props);
        this.state = {
            coupons: store.getState().CustomerState.customer,
            couponsPurchase: store.getState().customerPurchaseState.customerPurchase,
            user: store.getState().authState.user
        };
    }
   
    public async componentDidMount(): Promise<void> {
        console.log(this.state.user?.clientType);
        console.log(ClientType.customer);
        if (this.state.user?.clientType !== ClientType.customer) {
            notify.error(ErrMsg.PLS_LOGIN);
            //redirect to login
        }
        this.unsubscribe = store.subscribe(() => {
            this.setState({
                coupons: store.getState().CustomerState.customer,
                couponsPurchase: store.getState().customerPurchaseState.customerPurchase
            });
        });

        try {
            const response = await tokenAxios.get<CouponModel[]>(globals.urls.customers + "allCoupons"); //async request
            store.dispatch(customerDownloadedAction(response.data)); //update global state
            this.setState({ coupons: response.data }); //update local state
            notify.success(SccMsg.GET_COUPONS);

        } catch (err) {
            notify.error(ErrMsg.GET_COUPONS);
        }
        try {
            const response1 = await tokenAxios.get<CouponModel[]>(globals.urls.customers + "coupons"); //async request
            console.log(response1.status);
            store.dispatch(customerPurchaseDownloadedAction(response1.data)); //update global state
            this.setState({ couponsPurchase: response1.data }); //update local state
            notify.success(SccMsg.GET_COUPONS_PURCHASE);

        } catch (err) {
            notify.error(ErrMsg.GET_COUPONS_PURCHASE);
        }
    }
    public componentWillUnmount(): void {
        this.unsubscribe();
    }

    private async purchase(c: CouponModel) {
        const res =  window.confirm(
            "Are you sure you want to purchase coupon " + c.title + "?"
        );
        if (res) {
            try {
                await tokenAxios.post<any>(globals.urls.customers + "coupons/" + c.id);
                store.dispatch(customerPurchaseAddedAction(c)); // global state
                this.setState({ couponsPurchase: store.getState().customerPurchaseState.customerPurchase }); //local state

            } catch (err) {
                notify.error(ErrMsg.PURCHASE_COUPON);
            }
            try {
                const response = await tokenAxios.get<CouponModel[]>(globals.urls.customers + "allCoupons"); //async request
                store.dispatch(customerDownloadedAction(response.data)); //update global state
                this.setState({ coupons: response.data })
            } catch (err) {
                notify.error(ErrMsg.GET_COUPONS);
            }
        }
    }

    getDate(date: Date): string {
        const myDate = new Date(date);
        const year = myDate.getFullYear();
        const month = ("0" + (myDate.getMonth() + 1)).slice(-2);
        const day = ("0" + myDate.getDate()).slice(-2);
        const newDate = day + "-" + month + "-" + year;
        return newDate;
    }
    public render(): JSX.Element {
        return (
            <div className="Customer">
                <h1>Purchased Coupons</h1>
                {this.state.couponsPurchase.length === 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>CATEGORY</th>
                                <th>TITLE</th>
                                <th>PRICE</th>
                                <th>AMOUNT</th>
                                <th>DESCRIPTION</th>
                                <th>START_DATE</th>
                                <th>END_DATE</th>
                            </tr>
                        </thead>
                    </table>)}
                {this.state.couponsPurchase.length !== 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>CATEGORY</th>
                                <th>TITLE</th>
                                <th>PRICE</th>
                                <th>DESCRIPTION</th>
                                <th>START_DATE</th>
                                <th>END_DATE</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.couponsPurchase.map((c) => (
                                <tr key={c.id}>
                                    <td>{c.category}</td>
                                    <td>{c.title}</td>
                                    <td>{c.price}</td>
                                    <td>{c.description}</td>
                                    <td>{this.getDate(c.startDate)}</td>
                                    <td>{this.getDate(c.endDate)}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}

                <h1>ALL Coupons</h1>
                {this.state.coupons.length !== 0 && (
                    <table>
                        <thead>
                            <tr>
                                <th>CATEGORY</th>
                                <th>TITLE</th>
                                <th>PRICE</th>
                                <th>AMOUNT</th>
                                <th>DESCRIPTION</th>
                                <th>START_DATE</th>
                                <th>END_DATE</th>
                                <th>
                                    Action
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.coupons.map((c) => (
                                <tr key={c.id}>
                                    <td>{c.category}</td>
                                    <td>{c.title}</td>
                                    <td>{c.price}</td>
                                    <td>{c.amount}</td>
                                    <td>{c.description}</td>
                                    <td>{this.getDate(c.startDate)}</td>
                                    <td>{this.getDate(c.endDate)}</td>
                                    <td>
                                        <button onClick={() => this.purchase(c)}>Purchase</button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                )}
            </div>
        );
    }
}


export default Customer;
