import Main from "../Main/Main";
import "./Routing.css";
import { Redirect,Route, Switch } from "react-router-dom";
import Page404 from "../../ShardArea/Page404/Page404";
import Login from "../../AuthArea/Login/Login";
import EmptyView from "../../ShardArea/EmptyView/EmptyView";

import Logout from "../../AuthArea/Logout/Logout";

import AdminArea from "../../AdminArea/AdminArea";

import Customer from "../../CustomersArea/Customer/Customer";
import Company from "../../CompaniesArea/Company/Company";
import AddCompany from "../../AdminArea/AddCompany/AddCompany";
import AddCustomer from "../../AdminArea/AddCustomer/AddCustomer";
import UpdateCompany from "../../AdminArea/UpdateCompany/UpdateCompany";
import UpdateCustomer from "../../AdminArea/UpdateCustomer/UpdateCustomer";
import AddCompanyCoupon from "../../CompaniesArea/AddCompanyCoupon";
import UpdateCompanyCoupon from "../../CompaniesArea/UpdateCompanyCoupon/UpdateCompanyCoupon";
import CustomerPurchases from "../../CustomersArea/CustomerPurchases/CustomerPurchases";

function Routing(): JSX.Element {
    return (
        <div className="Routing">
			<Switch>
           <Route path="/home" component={Main} exact/>
           <Route path="/specials" component={EmptyView} exact/>
           <Route path="/login" component={Login} exact/>
           <Route path="/logout" component={Logout} exact/>
           <Route path="/admin" component={AdminArea} exact/>
           <Route path="/customers" component={Customer} exact/>
           <Route path="/companies" component={Company} exact/>       
           <Route path="/admin/company/add" component={AddCompany} exact />
           <Route path="/admin/customer/add" component={AddCustomer} exact />
           <Route path="/admin/company/update/:id" component={UpdateCompany} exact />
           <Route path="/admin/customer/update/:id" component={UpdateCustomer} exact />  
           <Route path="/companies/coupon/add" component={AddCompanyCoupon} exact />
           <Route path="/companies/coupon/update/:id" component={UpdateCompanyCoupon} exact />
          <Route path="/purchaseslist/:id" component={CustomerPurchases} exact/>
      
           <Redirect from='/' to='home' exact/>
           <Route component={Page404}/>
            </Switch>

        </div>
    );
}

export default Routing;
